.app__header-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.app__title{
    color: blueviolet;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 35px;
}
.input-filter{
    color: black;
    border: 4px solid purple;
    width: 30% ;
    height: 30px;
    font-size: 20px;
    font-family: Georgia, 'Times New Roman', Times, serif; 
    background-color: pink;
}
.app__filter-languages{
    margin-bottom: 20px;
    background-color: pink;
    width: 10%;
    height: 40px;
    font-size: 20px;
    border: 2px solid purple;
}
.app__list{
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
   
    
    
}
.app__list-item{
    border: 1px solid purple;
    margin-bottom: 10px;
    margin-right: 10px;
    width: 40%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}
.btn{
    border: 2px solid black;
    border-radius: 20px;
    background-color: purple;
    color:wheat;
    margin-top: 15px;
    padding: 5px;
    width: 160px ;

}
.repos{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.repos__name{
    font-family: Georgia, 'Times New Roman', Times, serif; 
    font-size: 15px;
}
.repos__info{
    font-family: Georgia, 'Times New Roman', Times, serif; 
    font-size: 15px;
    color:black;
    margin: 10px;
    text-align: center;
}
.repos__language{
    font-family: Georgia, 'Times New Roman', Times, serif; 
    font-size: 15px;
    color:black;

}
.html{
    background-image: url('./images/html.png');
    background-position: center; 
    background-repeat: no-repeat;
    background-size: contain;
    width: 50px;
    height: 50px;
     
}
.css{
    background-image: url('./images/css.png');
    background-position: center; 
    background-repeat: no-repeat;
    background-size: contain;
    width: 40px;
    height: 40px;
   
}
.javascript{
    background-image: url('./images/javascript.png');
    background-position: center; 
    background-repeat: no-repeat;
    background-size: contain;
    width: 80px;
    height: 70px;

}
.repos__card{
    background: #EECDA3;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #EF629F, #EECDA3);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #EF629F, #EECDA3); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    height:100vh ;
    font-family:  Georgia, 'Times New Roman', Times, serif; 

}
.repos__card-name{
    font-size: 30px;

}
.repos__card-info{
    font-size: 20px;
    color: purple;
    
}
.repos__card-language{
    font-size: 20px;
    color: purple;
    

}
.repos__card-stargazers{
    font-size: 20px;
    color: purple;
    
}
.repos__card-watchers{
    font-size: 20px;
    color: purple;
    
}
.repos__card-forks{
    font-size: 20px;
    color: purple;
    
}
